.titleum {
    background-color: black;
    width: 100%;
    height:120px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#conhecimentos{
    font-family: Kdam;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 730px;
    height: 90px;
    color: aliceblue;
    border: 2px solid;
    background-color: #333232;
    border-radius: 10px;
    font-size: 45px;
}


@media (max-width: 768px) {
    .titleum {
        background-color: black;
        width: 100%;
        height:80px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    #conhecimentos{
        font-family: Kdam;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 380px;
        height: 45px;
        color: aliceblue;
        border: 1px solid;
        background-color: #333232;
        border-radius: 5px;
        font-size: 26px;
    }
}
@media (min-width: 769px) and (max-width: 1024px ){
    .titleum {
        background-color: black;
        width: 100%;
        height:80px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    #conhecimentos{
        font-family: Kdam;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 380px;
        height: 65px;
        color: aliceblue;
        border: 1px solid;
        background-color: #333232;
        border-radius: 5px;
        font-size: 29px;
    }
}